/* eslint-disable jsx-a11y/alt-text */
// App.js
import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Scrollbar, Navigation } from "swiper/modules";
import "swiper/css";
import "swiper/css/scrollbar";
import "swiper/css/navigation";
import "./style.css";
import Card from "./Card"; // Card  import
import data from "./data"; //  import

export default function ProductAll() {
  //     
  const perfumeData = data.filter((item) => item.list === "f");
  const candleData = data.filter((item) => item.list === "c");

  return (
    <>
      <Swiper
        scrollbar={{ hide: false }}
        modules={[Autoplay, Scrollbar, Navigation]} //   
        spaceBetween={30} //   
        centeredSlides={true} //  
        loop={true} //    
        autoplay={{
          delay: 2500, // 2.5  
          disableOnInteraction: false, //    
        }}
        pagination={{ clickable: true }} //    
        navigation={true} //   
        className="mySwiper"
      >
        {/*   */}
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/001.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/002.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/003.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/004.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/005.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/006.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/007.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/008.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/009.jpg`} />
        </SwiperSlide>
      </Swiper>

      {/*   */}
      <h2>Perfume</h2>
      <div className="card-container">
        {perfumeData.map((item) => (
          <Card
            key={item.id}
            id={item.id}
            img={item.img}
            title={item.title}
            content={item.content}
            price={item.price}
            list={item.list} // list 
            category="f" //  
          />
        ))}
      </div>
      <img src={`${process.env.PUBLIC_URL}/img/banner.png`} alt="banner" />

      {/*   */}
      <h2>Candle</h2>
      <div className="card-container">
        {candleData.map((item) => (
          <Card
            key={item.id}
            id={item.id}
            img={item.img}
            title={item.title}
            content={item.content}
            price={item.price}
            list={item.list} // list 
            category="c" //  
          />
        ))}
      </div>
    </>
  );
}
